<template>
	<div id="app">
        <div class="wrapper clearfix">

            <players 
                v-bind:activePlayer="activePlayer"
                v-bind:currentScore="currentScore"
                v-bind:scorePlayers="scorePlayers"
            />

            <controls
                v-bind:isPlaying="isPlaying"
                v-on:handleNewGame="handleNewGame"
                v-on:handleRollDice="handleRollDice"
            />
            
            <dices 
                v-bind:dices="dices"
            />
            
            <popup-rule 
                v-on:handleConfirm="handleConfirm"
                v-bind:isOpenPopup="isOpenPopup"
            />
        </div>
	</div>
</template>

<script>
import Controls from './components/Controls.vue';
import Dices from './components/Dices.vue';
import Players from './components/Players.vue';
import popupRule from './components/popupRule.vue';
import swal from 'sweetalert';
export default {
  name: "app",
  data() {
    return {
      scorePlayers: [0, 0], //im tng ca PLAYER: index 0 l im tng PLAYER 1, index 1 l im tng PLAYER 2
      activePlayer: 0, //Nu 0 l PLAYER 1 ang chi, nu khc 0 th PLAYER 2 ang chi
      currentScore: 0, //Nu PLAYER 1 ang chi th currentScore = 0 s gn cho PLAYER 2 v ngc li
      isPlaying: false, //C ang chi hay khng? (false: khng, true: c)
      isOpenPopup: false, //Popup lut chi c ang m hay khng? (false: khng, true: c)
      dices: [5,6], //trng thi 2 mt xc xc
    };
  },
  components: {
    Players,
    Controls,
    Dices,
    popupRule,
  },
  methods: {
    handleNewGame(){ //khi n "New Game"
      this.isOpenPopup = true; //popup (lut chi) hin
    },
    handleConfirm(){  //khi n " hiu"
      this.isPlaying = true;      //trng thi ang chi dc bt
      this.isOpenPopup = false;   //tt popup(lut chi)
      this.activePlayer = 0;      //PLAYER 1 c active
      this.scorePlayers = [0,0];  //im tng ca ngi chi dc set v 0
      this.currentScore = 0;   //im mt xc xc set v 0
      this.dices = [1,1];   // 2 mt xc xc set v 1
    },
    nextPlayer(){
      this.activePlayer = this.activePlayer === 0 ? 1:0; //PLAYER ang chi c phi PLAYER 1 khng? phi th i thnh PLAYER 2, khng th PLAYER 1
      this.currentScore = 0;
    },
    handleRollDice(){ //khi n Roll Dice
      if(this.isPlaying == true){   //nu trng thi ang chi dc bt, ng ngha vi  bm "New Game" v " hiu"
        var dice1 = Math.ceil(Math.random()*6); //radom lm trn ln t 0-6
        var dice2 = Math.ceil(Math.random()*6);
        this.dices = [dice1, dice2]; //gn gi tr 2 mt xc xc bng 2 bin c radom  trn
        this.nextPlayer(); //i lt chi cho PLAYER sau khi quay xong 1 ln
        if(dice1 == 1 || dice2==1 ) //nu 1 trong 2 xc xc quay vo 1
        {
          setTimeout(function(){
          //thng bo cho ngi chi  quay trng  1 sau 350ms ( gi tr currentScore c set v 0 trc khi chy alert)
          swal('Bn  quay trng  1 mt ri :<');
          },350)
          //current PLAYER  = 0
          this.currentScore = 0;
        }
        else{
          //im tnh  currentScore
          this.currentScore = dice1 + dice2;
        }
        this.scorePlayers[this.activePlayer] += this.currentScore;
      }
      else{
        swal('Nhn vo New Game  bt u'); //nu PLAYER cha nhn vo "New Game" th Roll Dice khng hot ng, PLAYER s nhn dc li nhc
      }
    },
  }
};
</script>

<style>
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.clearfix::after {
  content: "";
  display: table;
  clear: both;
}

body {
  background-image: linear-gradient(
      rgba(62, 20, 20, 0.4),
      rgba(62, 20, 20, 0.4)
    ),
    url("/public/assets/back.jpg");
  background-size: cover;
  background-position: center;
  font-family: Lato;
  font-weight: 300;
  position: relative;
  height: 100vh;
  color: #555;
}

.wrapper {
  width: 1000px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #fff;
  box-shadow: 0px 10px 50px rgba(0, 0, 0, 0.3);
  overflow: hidden;
}
</style>



