<template>
    <div class="wrapper-popup" v-bind:class="getClassPopup">
        <div class="rule">
            <h3>Lut chi</h3>
            <p>
              Ngi chi: PLAYER 1 v PLAYER 2. Hy set im chin thng  FINAL SCORE trc nh! <br style="margin-bottom: 3px;">
              C 2 xc xc  quay trong tr chi, mi ln quay im ca xc sc c tnh  CURRENT, 
              tng im ca PLAYER c hin th ngay di tn PLAYER. <br style="margin-bottom: 3px;"><strong>Ch :</strong> Khi PLAYER quay 1 trong 2 xc xc 
              vo 1 im CURRENT nhn c l 0, ngha l + 0 vo im tng. <br style="margin-bottom: 3px;"> Bm ROLL DICE  quay xc xc, HOLD  dng lt.
            </p>
            <button 
                v-on:click="confirmButton" class="confirm"> hiu</button>
        </div>
    </div>
</template>

<script>
export default {
    name: 'popup-rule',
    props: {
        isOpenPopup: { type: Boolean, default: false }
    },
    data() {
        return {
        }
    },
    methods:{
        confirmButton(){
            this.$emit('handleConfirm');
        }
    },
    computed: {
        getClassPopup: function() {
            return {
                'open-popup': this.isOpenPopup
            }
        }
    }
}
</script>

<style>
    .wrapper-popup {
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color: rgba(0,0,0,0.4);
        opacity: 0;
        visibility: hidden;
        transition: all .3s ease;
    }
    .wrapper-popup.open-popup {
        opacity: 1;
        visibility: visible;
    }
    .rule {
        width: 350px;
        padding: 20px;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%) scale(1.2);
        background-color: #fff;
        position: absolute;
        font-family: 'Courier New', Courier, monospace;
        transition: all .3s ease;
    }
    .open-popup .rule {
        transform: translate(-50%, -50%) scale(1);
    }
    .rule h3 {
        background-color: #333;
        color: #fff;
        font-size: 27px;
        margin-bottom: 10px;
    }
    .rule .confirm {
        cursor: pointer;
        font-family: Arial, Helvetica, sans-serif;
        font-size: 12px;
        margin-top: 20px;
        padding: 8px 15px;
        border: 2px solid #333;
        background-color: #fff;
        transition: all .3s ease;
    }
    .rule .confirm:hover {
        color: #fff;
        background-color: #333;
    }
</style>


